<template>
  <div class="pageBlibli">
    <img
      src="@/assets/PageBlibli/img_part01.png"
      alt=""
      class="part1" />
    <div class="input">
      <img
        src="@/assets/PageBlibli/img_part02.jpg"
        alt=""
        class="part2" />
      <div class="form">
        <VanForm
          @submit="onSubmit"
          @failed="showErrorMsg"
          ref="form"
          validate-first
          :show-error="false"
          :show-error-message="false">
          <VanField
            v-model="form.phone"
            name="phone"
            placeholder="请填写手机号"
            :rules="[
              { required: true, message: '33003' },
              {
                pattern: phoneReg,
                message: '33004',
              },
            ]">
            <template #left-icon>
              <span class="prefix"> +86 <span class="icon">▼</span> </span>
            </template>
          </VanField>
          <VanField
            v-model="form.code"
            name="code"
            placeholder="请填写验证码"
            :rules="[{ required: true, message: '33005' }]">
            <template #button>
              <VanCountDown
                ref="countDown"
                class="button"
                millisecond
                :time="60000"
                :time-data="'timeData'"
                :auto-start="false"
                format="ss"
                @finish="finish">
                <template #default="timeData">
                  <span
                    class="block"
                    style="text-align: center;"
                    @click="start">{{ isFinsh ? "获取验证码" : timeData.seconds + "s" }}</span>
                </template></VanCountDown>
            </template>
          </VanField>
          <div style="margin: 16px;" @click="submit">
            <div class="submit">立即报名</div>
          </div>
        </VanForm>
      </div>
    </div>
    <img
      src="@/assets/PageBlibli/img_part03.jpg"
      alt=""
      class="part3" />
    <img
      src="@/assets/PageBlibli/img_part04.jpg"
      alt=""
      class="part4" />
    <BackDiaLog @backDiaLogSubmit="pay" />
    <PayTypeDialog @change="pay" :config.sync="config"></PayTypeDialog>
  </div>
</template>
<script>
import { getSearch } from '@/utils'
import PayTypeDialog from './components/SelectPayType' // 支付方式选择弹窗
import BackDiaLog from '@/components/BaiduABBusniess/backDialog' // 支付挽留弹框
import { codeMap } from './ErrorMsg'
export default {
  components: {
    PayTypeDialog,
    BackDiaLog
  },
  data: function () {
    return {
      form: {},
      isFinsh: true,
      config: {
        show: false
      }
    }
  },
  created () {
    this.form = {
      phone: localStorage.getItem('PageBlibli-phone'),
      code: localStorage.getItem('PageBlibli-code')
    }
    const needCheckOrder = getSearch('checkOrder')
    if (needCheckOrder) {
      this.changeQuery('checkOrder')
      this.checkOrder()
      setTimeout(() => {
        localStorage.setItem('backDialogMark', true)
        this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', true)
      }, 1300)
      this.timer = setInterval(() => {
        this.checkOrder()
      }, 3000)
    }
  },
  beforeDestroy () {
    clearInterval(this.timer)
  },
  methods: {
    submit () {
      this.$refs.form.submit()
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_toufang_lcx_0234'
      })
    },
    goSucccess () {
      this.$router.push({
        name: 'Officals',
        query: {
          ...this.$route.query
        }
      })
    },
    checkOrder () {
      if (!this.form.phone) return
      this.$apis.payment
        .checkOrder({
          phone: this.form.phone,
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .then((res) => {
          // 未支付
          console.log(res)
        })
        .catch((err) => {
          // 支付成功后
          const code = err.code
          clearInterval(this.timer)
          localStorage.removeItem('backDialogMark')
          this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', false)
          if (code === 30006) {
            this.goSucccess()
          } else {
            this.showErrorMsg(err)
          }
        })
    },
    // 获取验证码
    start () {
      if (!this.isFinsh) {
        return
      }
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_toufang_lcx_0233'
      })
      this.$refs.form
        .validate('phone')
        .then((res) => {
          this.$refs.countDown.start()
          this.isFinsh = false
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          this.showErrorMsg({ code: err.message })
        })
    },
    // 错误表单提示
    showErrorMsg (err) {
      let code = err.code
      let msg = ''
      if (err.errors) code = err.errors[0].message
      if (codeMap[code]) msg = codeMap[code].msg
      const message = err.msg || msg
      let confirmButtonText = '确定'
      if (code === 30006) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({
        message,
        confirmButtonText
      })
        .then(res => {
          this.$log.click(codeMap[code].report)
          if (code === 30006) {
            this.goSucccess()
          }
        })
    },
    finish (e) {
      this.$refs.countDown.reset()
      this.isFinsh = true
    },
    // 选择支付类型
    pay (payType) {
      if (payType) {
        localStorage.setItem('payType', payType)
      }
      payType = localStorage.getItem('payType')
      const query = getSearch()
      const url = location.href + '&checkOrder=yes'
      this.$apis.payment
        .pay({
          ...query,
          ...this.form,
          pay_type: payType,
          return_url: encodeURIComponent(url),
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .then((data) => {
          if (payType === '1') {
            this.aliPay(data)
          }
          if (payType === '2') {
            this.wxPay(data)
          }
          this.$store.commit('ABTest/SET_BACK_DIALOG_MARK', true)
        })
        .catch((err) => {
          this.showErrorMsg(err)
        })
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.push({
        ...this.$route,
        query: {
          ...query
        }
      })
    },
    // 微信支付
    wxPay (data) {
      window.location.replace(decodeURIComponent(data.url))
    },
    // 支付宝支付
    aliPay (data) {
      this.changeQuery('checkOrder', 'yes')
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) {
        isReSet.parentNode.removeChild(isReSet)
      }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      div.children[0].submit()
    },
    onSubmit (e) {
      // 校验手机号和验证码
      this.$apis.payment
        .payBeforeCheck(this.form)
        .then(async (res) => {
          this.config.show = true
          localStorage.setItem('PageBlibli-phone', this.form.phone)
          localStorage.setItem('PageBlibli-code', this.form.code)
        })
        .catch((err) => {
          this.showErrorMsg(err)
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.pageBlibli {
  img {
    vertical-align: bottom;
    width: 100%;
  }

  .part1 {
    width: 100%;
  }

  .input {
    position: relative;

    .part2 {
      width: 100%;
    }

    .form {
      position: absolute;
      width: 624px;
      left: 50%;
      top: 60px;
      height: 350px;
      box-sizing: border-box;
      transform: translateX(-50%);
      padding: 45px;
      padding-bottom: 30px;

      .prefix {
        font-size: 35px;
        font-weight: 500;
        color: #4d4dab;
        display: flex;
        align-items: center;

        .icon {
          font-size: 25px;
          margin-left: 10px;
        }

        padding-right: 15px;
      }

      .submit {
        width: 447px;
        height: 77px;
        background: #bcd0ff;
        border-radius: 24px;
        font-size: 45px;
        font-weight: bold;
        color: #4d4dab;
        line-height: 78px;
        text-align: center;
      }

      .button {
        width: 260px;
        height: 78px;
        vertical-align: top;
        background: #d1deff;
        line-height: 80px;
        padding-left: 20px;
        background: chartreuse;
        position: absolute;
        right: -40px;
        top: 50%;
        background: #bcd0ff;
        transform: translateY(-50%);
        font-size: 38px;
        font-family: Source Han Sans CN;
        font-weight: 400;
        color: #4d4dab;
      }

      /deep/ .van-field__button {
        width: 230px;
      }

      /deep/ .van-cell {
        margin-bottom: 20px;
        width: 530px;
        height: 77px;
        border-radius: 20px;
        display: flex;
        align-items: center;
        font-size: 38px;
        font-weight: 400;
        color: #b4bed5;

        &::after {
          display: none;
        }
      }
    }
  }
}
</style>
